<template>
  <div class="user">
    <h1>{{profil.name}}</h1>
    <img :src="profil.avatar_url" width="200">
    <ul>
      <li>Username: {{username}}</li>
      <li>Member since {{profil.created_at}}</li>
    </ul>
    <ul>
      <li>
        Repository: {{stat.repo}}/
        <form v-on:submit.prevent="setResolution">
          <input type="number" v-model="resolution.repo">
        </form>
        {{ (resolution.repo === 0) ? '0' : parseInt(stat.repo/resolution.repo*100)+'%' }}
      </li>
      <li>
        Commit (2021): {{stat.commit}}/
        <form v-on:submit.prevent="setResolution">
          <input type="number" v-model="resolution.commit">
        </form>
        {{ (resolution.commit === 0) ? '0' : parseInt(stat.commit/resolution.commit*100)+'%' }}
      </li>
      <li>
        Star: {{stat.star}}/
        <form v-on:submit.prevent="setResolution">
          <input type="number" v-model="resolution.star">
        </form>
        {{ (resolution.star === 0) ? '0' : parseInt(stat.star/resolution.star*100)+'%' }}
      </li>
      <li>
        Fork: {{stat.fork}}/
        <form v-on:submit.prevent="setResolution">
          <input type="number" v-model="resolution.fork">
        </form>
        {{ (resolution.fork === 0) ? '0' : parseInt(stat.fork/resolution.fork*100)+'%' }}
      </li>
      <li>
        Follower: {{stat.follower}}/
        <form v-on:submit.prevent="setResolution">
          <input type="number" v-model="resolution.follower">
        </form>
        {{ (resolution.follower === 0) ? '0' : parseInt(stat.follower/resolution.follower*100)+'%' }}
      </li>
      <li>
        Pull Request: {{stat.pull_req}}/
        <form v-on:submit.prevent="setResolution">
          <input type="number" v-model="resolution.pull_req">
        </form>
        {{ (resolution.pull_req === 0) ? '0' : parseInt(stat.pull_req/resolution.pull_req*100)+'%' }}
      </li>
    </ul>
    <button @click="logout">Logout</button>
  </div>
</template>

<script>
import axios from 'axios'
import firebase from './../services/firebase'
export default {
  data () {
    return {
      username: '',
      fbid: '', // firestore collection id
      profil: {},
      stat: {
        repo: '',
        commit: '',
        star: '',
        fork: '',
        follower: '',
        pull_req: ''
      },
      resolution: {
        repo: 0,
        commit: 0,
        star: 0,
        fork: 0,
        follower: 0,
        pull_req: 0
      }
    }
  },
  methods: {
    setResolution () {
      if (navigator.onLine) {
        const db = firebase.firestore()
        db.collection('resolution')
          .doc(this.fbid)
          .update({
            resolution: this.resolution
          })
          .then(() => {
            // console.log("Document successfully updated!")
          })
          .catch((error) => {
            console.error('Error updating document: ', error)
          })
      }
    },
    loadGithubProfile () {
      if (navigator.onLine) {
        axios
          .get('https://api.github.com/users/' + this.username)
          .then(res => {
            this.profil = JSON.parse(JSON.stringify(res.data))
            this.stat.follower = this.profil.followers
          })
      }
    },
    loadGithubRepos () {
      if (navigator.onLine) {
        axios
          .get('https://api.github.com/users/' + this.username + '/repos')
          .then(res => {
            // this.post = JSON.parse(JSON.stringify(res.data))
            let repos = JSON.parse(JSON.stringify(res.data))
            this.stat.repo = repos.length
            // counting stars
            let stars = 0
            repos.forEach((e) => {
              stars += e.stargazers_count
            })
            this.stat.star = stars
            // counting forks
            let forks = 0
            repos.forEach((e) => {
              forks += e.forks_count
            })
            this.stat.fork = forks
          })
      }
    },
    loadGithubEvents () {
      if (navigator.onLine) {
        axios
          .get('https://api.github.com/users/' + this.username + '/events?per_page=100')
          .then(res => {
            let events = JSON.parse(JSON.stringify(res.data))
            let commits = 0
            let pullReqs = 0
            events.forEach((e) => {
              if (e.type === 'PushEvent' && e.created_at.substr(0, 4) === '2021') {
                commits += 1
              }
              if (e.type === 'PullRequestEvent' && e.created_at.substr(0, 4) === '2021') {
                pullReqs += 1
              }
            })
            this.stat.commit = commits
            this.stat.pull_req = pullReqs
          })
      }
    },
    loadResolution () {
      if (navigator.onLine) {
        const db = firebase.firestore()
        db.collection('resolution')
          .get()
          .then((q) => {
            q.forEach((doc) => {
              if (doc.data().username === this.username) {
                this.resolution = doc.data().resolution
                this.fbid = doc.id
              }
            })
          })
          .catch((error) => {
            console.error('Error getting documents: ', error)
          })
      }
    },
    logout () {
      localStorage.removeItem('gr_username')
      this.$router.push({ path: '/' })
    }
  },
  mounted () {
    if (!localStorage.gr_username) { // jika tidak ada data local
      this.$router.push({ path: '/' })
    }
    this.username = localStorage.getItem('gr_username')
    this.loadGithubProfile()
    this.loadGithubRepos()
    this.loadGithubEvents()
    this.loadResolution()
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped="">

</style>



