<template>
  <div class="home">
  	<h1>Home</h1>
    <div v-if="is_login">
      <button @click="$router.push({ path: username })">Resolusi</button>
    </div>
    <div v-else>
      <form v-on:submit.prevent="onSubmit">
        <input type="text" v-model="username">
        <button type="submit">OK</button>
      </form>
    </div>
  </div>
</template>

<script>
import firebase from './../services/firebase'
export default {
  data () {
    return {
      username: '',
      is_login: false
    }
  },
  methods: {
    onSubmit () {
      if (navigator.onLine) {
        localStorage.setItem('gr_username', this.username)
        const db = firebase.firestore()
        // cek apa sudah ada username di db
        db.collection('resolution')
          .get()
          .then((q) => {
            let userExist = false
            q.forEach((doc) => {
              if (doc.data().username === this.username) {
                userExist = true
              }
            })
            if (!userExist) { // kalau gak ada di db
              // insert baru
              db.collection('resolution')
                .add({
                  username: this.username,
                  resolution: {
                    repo: 0,
                    commit: 0,
                    star: 0,
                    fork: 0,
                    follower: 0,
                    pull_req: 0
                  }
                })
                .then(() => {
                  this.is_login = true
                  this.$router.push({ path: this.username })
                })
                .catch((error) => {
                  console.error('Error updating document: ', error)
                })
            } else {
              this.is_login = true
              this.$router.push({ path: this.username })
            }
          })
          .catch((error) => {
            console.error('Error getting documents: ', error)
          })
      }
    }
  },
  mounted () {
    if (localStorage.gr_username) {
      this.username = localStorage.getItem('gr_username')
      this.is_login = true
      // setTimeout(() => {
      //   this.$router.push({ path: this.username })
      // }, 2000)
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped="">

</style>



